.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.app-header {
  background-color: #261F1F;
  min-height: 3rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white;
}

.app-header h5 {
  display: inline-block;
  padding-left: 2rem;
}

.app-header :not(h5){
  width: 20%;
  height: 20%;
  object-fit: cover;
  border-radius: 0.5rem; 
  margin-left: auto;
  flex-direction: row-reverse;
}

.app-footer {
  background-color: #261F1F;
  min-height: 3rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white;
}

.app-footer h5 {
  display: inline-block;
  padding-left: 2rem;
}

.app-footer :not(h5){
  width: 20%;
  height: 20%;
  object-fit: cover;
  border-radius: 0.5rem; 
  margin-left: auto;
  flex-direction: row-reverse;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body, html, #root {
  margin: 0;
  height: 100%;
  font-family: sans-serif;
}

.app-container {
  display: flex;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  background-color: #7D6E6E;
  color: white;
  flex-wrap: wrap;
}

@media (max-width: 600px) {

  .app-container {
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .app-container > * {
    flex-basis: auto;
    width: 100%;
  }

  .projectlist-container {
    max-height: 30vh;
    overflow: scroll;
    padding: 0;
  }
}

.app-container > :first-child {
  flex: 1;
  background-color: #7D6E6E;
  color: white;
  padding: 1rem;
}

@media (max-width: 600px) {
  .app-container > :first-child {
    flex: 1;
    background-color: #7D6E6E;
    color: white;
    padding: 0rem;
  }
}

.app-container > :nth-child(2) {
  flex: 4;
}

.heroimage {
  display: flex;
  position: relative;
  width: 100%;
  max-height: 50vh;
}

.heroimage img {
  width: 100%;
  max-height: 100%;
  object-fit: cover;
  object-position: center; 
  filter: brightness(80%);
  border-radius: 1rem;
}

.heroimage img:hover{
  filter: brightness(75%) contrast(80%) saturate(60%);
  transition: filter 1s ease;
}

.hero-label {
  position: absolute;
  bottom: 0.5rem;
  right: 0.5rem;
  z-index: 2;
  background: #BFBCBC;
  color: white;
  padding: 0.25rem 0.5rem;
  font-size: clamp(2rem, 2vw, 4rem);
  border-radius: 4px;
  white-space: nowrap;
  transition: opacity 0.3s ease-in-out;
  opacity: 0.0;
  text-shadow: 0 0 6px rgba(0, 0, 0, 0.8);
}

.heroimage:hover .hero-label{
  opacity: .8;
  transition-property: transform, opacity;
  transition-duration: 0.4s, 0.6s;
  transition-timing-function: ease-in-out;
  transition-delay: 0s, 0.2s;
}

.hero-project {
  position: absolute;
  top: 0rem;
  z-index: 2;
  background: #4E3F3F;
  color: white;
  padding: 0.25rem 0.5rem;
  font-size: clamp(1rem, 2vw, 4rem);
  border-radius: 4px;
  white-space: nowrap;
  transition: opacity 0.3s ease-in-out;
  opacity: 0.9;
  text-shadow: 0 0 6px rgba(0, 0, 0, 0.8);
  overflow: auto;
}

.projectlist-container {
  position: relative;
  font-style: italic;
  background-color: #261F1F;
  color: whitesmoke;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding: .1rem;
  border-radius: 1rem;
  border-top-right-radius: 0%;
}

.projectlist-container h3{
  position: absolute;
  bottom: 100%;
  right: 0%;
  font-size: small;
  background-color: #845A5A;
  border-radius: .5rem;
  padding: .2rem;
  margin: 0;
  color: rgb(247, 247, 247);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}

.projectlist-items {
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 2.5rem;
}

.projectlist-item {
  border-radius: .5rem;
  background-color: rgb(48, 45, 42);
  padding: 1rem;
}

.projectlist-items a:link{
  color: white;
  text-decoration: none;
}

.projectlist-items a:visited{
  color: white;
  text-decoration: none;
}

.projectlist-items a:hover{
  color: white;
  text-decoration: none;
}

.projectlist-items a:active{
  color: white;
  text-decoration: none;
}

.projectlist-items img {
  width: 100%;
  max-width: 5rem;  
  height: auto;
  aspect-ratio: 1 / 1;
  object-fit: cover;
  border-radius: 0.5rem; 
}

.projectlist-item p:first-of-type{
  font-size: 1.25rem;
}

.sectiontext {
  display: flex;
  flex-direction: column;
  gap: 0;
  line-height: 1.2;
  background-color: #7D6E6E;
  max-height: 50vh;
  font-size: clamp(.1rem, 2vw, .6rem);
  overflow: visible;
}

.sectiontext p {
  display: block;
  color: white;
}

.sectiontext p:first-of-type{
  display: block;
  color: white;
  margin: .75rem;
  font-size: 2em;
}

.sectiontext h2 {
  color: white;
  margin: 0rem;
  background-color: #4E3F3F;
  border-radius: .5rem;
  padding: .5rem;
  margin-top: .5rem;
}

.switcher{
  display: flex;
  margin-top: 1rem;
  margin-left: 1rem;
}

.switcher span{
  display: flex;
  justify-content: space-around;
  font-family: 'Courier New', Courier, monospace;
  color: #888;
  letter-spacing: 0.5px;
  margin-right: 1rem;
}

.switcher span:first-of-type{
  color: white;
  margin-right: 0;
}

.missiontext {
  margin: 1rem;
  padding: 1rem;
  color: white;
  border-radius: 1rem;
  background-color: #1e1e1e;
  font-family: 'Courier New', Courier, monospace;
  border-radius: 0.5rem;
  line-height: 1.5;
  white-space: pre-wrap;
}

.missiontext span, p {
  font-family: 'Courier New', Courier, monospace;
}

.cursor {
  display: inline;
  width: 1ch;
  animation: blink 1s steps(2, start) infinite;
}

@keyframes blink {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

.missionblock{
  background-color: #424B52;
  font-size: clamp(.5rem, 2vw, 1.5rem);
  padding: .5rem;
  border-radius: 1rem;
  margin: .1rem;
}

.pane {
  height: 100%;
  background-color: #7D6E6E;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.pane > :nth-child(1){
  background-color: #7D6E6E;
  flex: 3;
}

.pane > :nth-child(2){
  color: black;
  flex: 1;
}

.pane > :nth-child(3){
  color: black;
  flex: 3;
}

.icon {
  max-width: 10rem;
  max-height: 10rem;
  filter: brightness(0) invert(1);
}